import React, { memo, cloneElement, DOMAttributes, ReactElement } from 'react';
import { IRestProps, mergeRestProps } from '../../common';

export type IIconMobileProps = { size?: number, color?: string } & IRestProps & DOMAttributes<SVGElement>;

type IIconMobileInnerProps = { svg24: ReactElement };

export const IconMobile = memo(
  (props: IIconMobileProps & IIconMobileInnerProps) => {
    const {
      size = 24,
      color,
      svg24 = <svg />,
      ...rest
    } = props;

    return cloneElement(
      svg24,
      {
        width: size,
        height: size,
        ...mergeRestProps(rest, 'wdn-icon-mobile', color ? { color } : undefined)
      }
    );
  }
);
IconMobile.displayName = 'IconMobile';